<template>
  <button class="js-back-to-top back-to-top" id="myBtn" title="" @click="back"></button>
</template>

<script>
import $ from "jquery";
export default {
  name: "backToTop",
  mounted() {
    $('.wrapper')[0].onscroll = () => {
      this.showButton();
    };
  },
  methods: {
    back() {
       $('.wrapper').stop().animate({ scrollTop: 0 }, 500, "swing");
    },
    showButton() {
      if (
        $('.wrapper')[0].scrollTop > 50
      ) {
        document.getElementById("myBtn").style.display = "block";
      } else {
        document.getElementById("myBtn").style.display = "none";
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.back-to-top {
  display: none;
  /*  */
  position: fixed; /*  */
  bottom: 10%; /*  */
  right: 5%; /*  */
  z-index: 99999; /*  */
  border: 1px solid #5cb85c; /*  */
  outline: none; /*  */
  background-color: rgba(255, 255, 255, 20%); /*  */
  color: #5cb85c; /*  */
  cursor: pointer; /*  */
  padding: 10px 15px 15px 15px!important; /*  */
  border-radius: 6px; /*  */
}

/* .back-to-top:hover {
  background-color: #5cb85c; 
  color: #fff;
} */
</style>


